<template>
    <div>
        <div class="no-data text-c" v-if="noData">
            <img :src="bg" alt="">
            <p class="f32 text-c pt30">暂无订单~</p>
        </div>
        <div class="my-order">
            <orderList :order="order" isShowPay="1" @click="toOrderDetail"></orderList>
        </div>
    </div>
</template>

<script>
    import orderList from '@/components/orderList.vue'
    import NoData from '@/assets/images/bg_no_data02.png'

    export default {
        name: "myOrder",
        data() {
            return {
                //订单列表
                order: [],
                page: 1,
                bg: NoData,
                noData: false,   //数据为空缺省页
                dataFlag: true   //禁止请求
            }
        },
        methods: {
            /**
             * 获取我的订单
             * @param page  页数
             */
            getOrder(page = 1) {
                let that = this;
                this.$toast({
                    type: 'loading',
                    message: '数据加载中...',
                    duration: 0,
                    loadingType: 'spinner'
                });
                // that.dataFlag = false;
                this.$ajax.post('/User/api', {
                    api_name: 'orderList',
                    token: window.localStorage.token,
                    page: page,
                    pageseze: 10
                }).then((res) => {
                    that.$toast.clear();
                    that.dataFlag = true;
                    console.log(res, '我的订单');
                    if (res.data.code == 1) {
                        let data = res.data.data;
                        //没有订单显示缺省页
                        if (data.length == 0 && that.page == 1) {
                            that.noData = true;
                            that.dataFlag = false;
                            return
                        } else if (data.length == 0) {
                            that.dataFlag = false;
                            that.$toast('没有数据啦');
                            return
                        }
                        data.forEach((item) => {
                            that.order.push(item);
                        });
                        that.page++;
                    } else {
                        that.dataFlag = true;
                    }
                }).catch(() => {
                    that.$toast.clear();
                });
            },

            /**
             * 下拉刷新
             *
             */
            onScroll() {
                //可滚动容器的高度
                let innerHeight = document.getElementById('app').clientHeight;
                //屏幕尺寸高度
                let outerHeight = document.documentElement.clientHeight;
                //可滚动容器超出当前窗口显示范围的高度
                let scrollTop = document.documentElement.scrollTop;
                //scrollTop在页面为滚动时为0，开始滚动后，慢慢增加，滚动到页面底部时，出现innerHeight < (outerHeight + scrollTop)的情况，严格来讲，是接近底部。
                // console.log(innerHeight + " " + outerHeight + " " + scrollTop);
                if (innerHeight <= (outerHeight + scrollTop)) {
                    //加载更多操作
                    if (this.dataFlag) {
                        this.getOrder(this.page);
                    }
                }
            },


            /**
             * 跳转至订单详情
             * @param orderId
             */
            toOrderDetail(orderId) {
                this.$router.push({
                    path: '/orderDetail',
                    query: {id: orderId}
                })
            }
        },
        created() {
            this.getOrder();
            window.addEventListener('scroll', this.onScroll);
        },
        components: {
            orderList
        }
    }
</script>

<style scoped lang="less">
    .no-data {
        height: 100vh;
        background: #fff;

        img {
            width: 4.2rem;
        }
    }
</style>