<template>
    <!--售后申诉-->
    <div class="after-sale">
        <div class="order mb20">
            <p class="f30 mb45">订单编号：{{orderNumber}}</p>
            <div class="pos-rel">
                <textarea v-model="question" maxlength="140" @input="limitWord"
                          placeholder="请输入您的故障问题，并留下联系方式，我们会尽快回复您！"></textarea>
                <span class="word-limit color-999 f24">{{wordNumber}}/140</span>
            </div>


            <div class="upload">
                <div class="addPic">
                    <input type="file" @change="uploadPicture" accept="image/*"/>
                    <i class="iconfont icon-jia"></i>
                </div>
                <div v-for="(item,index) in imgArr" :key="index">
                    <img :src="item" alt="">
                </div>
                <!--<div>-->
                <!--<img :src="testImg" alt="">-->
                <!--</div>-->
                <!--<div>-->
                <!--<img :src="testImg" alt="">-->
                <!--</div>-->
            </div>
        </div>

        <input type="text" placeholder="请输入手机号码" v-model="mobile">
        <dlc-btn msg="提交" @click="submit"></dlc-btn>
    </div>
</template>

<script>
    import Button from '@/components/button.vue'
    import axios from 'axios'

    export default {
        name: "after-sale",
        data() {
            return {
                files: '',
                orderNumber: '',    //订单编号
                question: '', //问题
                mobile: '', //手机号
                wordNumber: 0,    //textarea已输入字数
                testImg: 'https://img.yzcdn.cn/upload_files/2017/03/16/Fs_OMbSFPa183sBwvG_94llUYiLa.jpeg?imageView2/2/w/100/h/100/q/75/format/jpg',
                imgArr: [],  //已上传图片
                imgPath: []
            }
        },
        methods: {
            /**
             *
             * 监听textarea字数变化
             */
            limitWord() {
                this.wordNumber = this.question.length;
            },

            /**
             * 图片上传
             */
            uploadPicture(e) {
                let that = this;
                let file = e.target.files[0];
                let param = new FormData(); // 创建form对象
                param.append("image[]", file, file.name); // 通过append向form对象添加数据
                let reader = new FileReader();
                reader.readAsDataURL(file);
                // reader.onloadend = function (evt) {
                //     console.log(evt.target)
                //     _this.imgs = evt.target.result;
                //     _this.show = false;
                // };
                let config = {
                    headers: {"Content-Type": "multipart/form-data"}
                };
                axios.post("http://sdxddshg.app.xiaozhuschool.com/wxsite/user/uploadImg", param, config
                ).then(res => {
                    console.log(res, '图片上传');
                    if (res.data.code == 1) {
                        that.imgArr.push(res.data.data.url);
                        that.imgPath.push(res.data.data.path);
                        that.$toast.success(res.data.msg);
                    } else {
                        that.$toast.fail(res.data.msg);
                    }
                });
            },


            /**
             *提交订单反馈
             */
            submit() {

                if (!this.question) {
                    this.$toast('请输入故障问题');
                    return false
                }
                if (!this.mobile) {
                    this.$toast('请输入手机号');
                    return
                }
                if (/^1[3|4|5|6|7|8][0-9]\d{8}$/.test(this.mobile)) {
                    let that = this;
                    this.$ajax.post('/User/api', {
                        api_name: 'feedback',
                        token: window.localStorage.token,
                        content: that.question,
                        phone: that.mobile,
                        order_no: that.orderNumber,
                        img_url: that.imgPath.join()+','
                    }).then((res) => {
                        if (res.data.code == 1) {
                            that.question = '';
                            that.mobile = '';
                            that.imgPath = [];
                            that.imgArr = [];
                            this.$toast.success(res.data.msg);

                        } else {
                            this.$toast.fail(res.data.msg);
                        }
                    })
                } else {
                    this.$toast('请输入正确的手机号');
                }
            }
        },
        created() {
            this.orderNumber = this.$route.query.id;
        },
        components: {
            'dlc-btn': Button
        }
    }
</script>

<style scoped lang="less">
    .after-sale {
        padding-bottom: 2rem;

        .order {

            background-color: #ffffff;
            padding: .52rem .3rem .38rem .3rem;

            textarea {
                display: block;
                width: 100%;
                height: 2.24rem;
                font-size: .24rem;
                line-height: 1.4;
                padding: .36rem .3rem .25rem .3rem;
                margin-bottom: .36rem;
                border: 1px solid rgba(238, 238, 238, 1);
                border-radius: .1rem;
            }

            .word-limit {
                position: absolute;
                right: .34rem;
                bottom: .25rem;
            }

        }


        .upload {
            display: flex;
            flex-wrap: wrap;

            > div {
                width: 1.2rem;
                height: 1.2rem;
                margin-right: .2rem;
                margin-bottom: .2rem;
                border-radius: .1rem;
                img {
                    width: 100%;
                    height: 1.2rem;
                    border-radius: .1rem;
                }
            }

            > div:nth-of-type(5n) {
                margin-right: 0;
            }
        }

        .addPic {

            border: 1px dashed rgba(207, 207, 207, 1);

            position: relative;

            input {
                z-index: 999;
                position: absolute;
                opacity: 0;
                height: 100%;
            }

            .iconfont {
                position: absolute;
                color: #999999;
                font-size: .8rem;
                top: .16rem;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        input {
            width: 100%;
            padding-left: .3rem;
            height: .8rem;
            font-size: .28rem;
            background-color: #ffffff;
            margin-bottom: 3.16rem;
        }
    }
</style>