<template>
    <!--绑定手机 Or 登录-->
    <div class="binding-phone">
        <!--已经绑定-->
        <div class="has-bound" v-if="isBinding">
            原绑定号码：{{oldPhone}}
        </div>

        <div class="input-box">
            <div class="pos-rel">
                <i class="iconfont icon-mobilefill"></i>
                <input type="text" v-model="mobile" class="mb20" placeholder="请输入手机号码" autofocus>
            </div>
            <div class="flex-between">
                <div class="pos-rel">
                    <i class="iconfont icon-lock"></i>
                    <input type="text" placeholder="请输入验证码" v-model="verifyCode">
                </div>
                <button @click="sendVerificationCode">{{code.title}}</button>
            </div>
        </div>
        <dlc-btn msg="确定" :type="buttonType" @click.native="bindPhone"></dlc-btn>


    </div>
</template>

<script>
    import Button from '@/components/button.vue'

    export default {
        name: "bindingPhone",
        data() {
            return {
                isBinding: false, //是否绑定过手机号
                oldPhone: '',    //原来绑定的手机号
                mobile: '',  //手机号
                verifyCode: '',  //验证码
                code: {
                    flag: false,
                    title: '获取短信验证码'
                },
                buttonType: 'disable'
            }
        },
        components: {
            'dlc-btn': Button,

        },
        methods: {

            //发送验证码
            sendVerificationCode() {
                if (!this.mobile) {
                    this.$toast('请输入手机号');
                    return
                }
                if (/^1[3|4|5|6|7|8][0-9]\d{8}$/.test(this.mobile)) {
                    this.buttonType = 'default';
                } else {
                    this.buttonType = 'disable';
                    this.$toast('请输入正确的手机号码');
                    return
                }
                if (this.code.flag) {
                    return false
                }

                let timer = 10;
                this.isBinding ? this.sendCodeAjax(1) : this.sendCodeAjax(2);
                // this.sendCodeAjax(2);
                let interTimer = setInterval(() => {
                    this.code.title = `${timer--}s`;
                    this.code.flag = true;
                    if (timer <= 0) {
                        clearInterval(interTimer);
                        this.code.title = '重新获取短信验证码';
                        this.code.flag = false;
                    }
                }, 1000);
            },
            /**
             *发送验证码AJAX
             * @param type  1=登录2=绑定
             */
            sendCodeAjax(type) {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'sendSmsCaptcha',
                    phone: this.mobile,
                    token: window.localStorage.token,
                    type: type
                }).then(function (res) {
                    console.log(res, '验证码');
                    that.$toast(res.data.msg);
                })
            },

            /**
             * 绑定手机号
             */
            bindPhone() {
                let that = this;
                if (!this.verifyCode) {
                    this.$toast('请输入验证码');
                    return false
                }
                this.$toast({
                    type: 'loading',
                    message: '绑定中...',
                    duration: 0,
                    loadingType: 'spinner'
                });
                this.$ajax.post('/User/api', {
                    api_name: 'userMobileSet',
                    token: window.localStorage.token,
                    mobile: this.mobile,
                    sms_code: this.verifyCode
                }).then(function (res) {
                    console.log(res, '绑定手机');
                    that.$toast.clear();
                    if (res.data.code == 1) {
                        that.$toast.success(res.data.msg);
                        that.$router.push({
                            path: '/my'
                        });
                    } else {
                        that.$toast.fail(res.data.msg);
                    }
                }).catch(() => {
                    that.$toast.clear();
                })
            },


            /**
             * 获取用户信息
             */
            getUserInfo() {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'userInfo',
                    token: window.localStorage.token
                }).then((res) => {
                    console.log(res, '用户信息');
                    let data = res.data.data;
                    if (res.data.code == 1 && data.mobile !== '') {
                        that.isBinding = true;
                        that.oldPhone = data.mobile;
                    }
                })
            }

        }
        ,
        created() {
            this.getUserInfo()
        }
    }
</script>

<style scoped lang="less">
    .binding-phone {
        .has-bound {
            font-size: .28rem;
            padding-left: .25rem;
            color: #666666;
            height: .9rem;
            line-height: .9rem;
            background-color: #EEEEEE;
        }

        .input-box {
            background-color: #f7f7f7;
            padding: .25rem .3rem .25rem .35rem;
            margin-bottom: 6rem;

            input {
                width: 100%;
                height: .9rem;
                line-height: .9rem;
                padding-left: .78rem;
                font-size: .28rem;
                background-color: #ffffff;
            }

            .iconfont {
                position: absolute;
                font-size: .4rem;
                left: .3rem;
                color: #B2B2B2;
            }

            .icon-mobilefill {
                top: .24rem;
            }

            .icon-lock {
                top: 50%;
                transform: translateY(-50%);
            }

            .flex-between {
                input {
                    width: 3.68rem;
                    margin-right: .18rem;
                }

                button {
                    width: 2.98rem;
                    height: .9rem;
                    font-size: .28rem;
                    color: #ffffff;
                    background-color: #CF6637;
                }
            }
        }
    }
</style>