<template>
    <!--合作加盟-->
    <div class="cooperation bg-fff" ref="cooperation">
        <label>联系人</label>
        <input type="text" placeholder="请输入联系人姓名" v-model="user">
        <label>联系电话</label>
        <input type="text" placeholder="请输入联系电话" v-model="phone">
        <label>备注</label>
        <textarea placeholder="请输入合作意向、优势等" v-model="remarks"></textarea>
        <dlc-btn msg="提交" @click="submit"></dlc-btn>
        <div class="pt30">
            <p class="f22 color-999 text-c">平台电话：<span>{{serviceTel}}</span></p>
        </div>
    </div>
</template>

<script>
    import dlcButton from '@/components/button.vue'

    export default {
        name: "cooperation",
        data() {
            return {
                clientHeight: '',   //页面高度
                user: '',   //联系人
                phone: '',  //电话
                remarks: '',  //备注
                serviceTel: ''  //平台电话
            }
        },
        methods: {
            changeFixed(clientHeight) {
                this.$refs.cooperation.style.height = clientHeight + 'px';
            },


            /**
             * 获取客服电话
             */
            getPhone() {
                this.$ajax.post('/Public/get_phone').then((res) => {
                    this.serviceTel = res.data.data.tel;
                })
            },

            /**
             * 提交表单ajax
             */
            formAjax() {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'cooperation',
                    token: window.localStorage.token,
                    user_name: this.user,
                    user_phone: this.phone,
                    content: this.remarks
                }).then(function (res) {
                    if (res.data.code == 1) {
                        that.$toast.success('提交成功');
                    } else {
                        that.$toast(res.data.msg)
                    }
                })
            },

            //    提交表单
            submit() {

                if (!this.user) {
                    this.$toast('请输入姓名');
                    return false
                }
                if (!this.phone) {
                    this.$toast('请输入联系电话');
                    return false
                }
                if (!this.remarks) {
                    this.$toast('请输入备注');
                    return false
                }

                if (/^1[3|4|5|6|7|8][0-9]\d{8}$/.test(this.phone)) {
                    this.formAjax();
                } else {
                    this.$toast('请输入正确的手机号码');
                    return false
                }


            }
        },
        created() {
            this.getPhone();
        },
        mounted() {
            // 获取浏览器可视区域高度
            this.clientHeight = `${document.documentElement.clientHeight}`;
            window.onresize = function temp() {
                this.clientHeight = `${document.documentElement.clientHeight}`;
            };
        },
        watch: {
            clientHeight: function () {
                this.changeFixed(this.clientHeight)
            }
        },
        components: {
            'dlc-btn': dlcButton
        }
    }
</script>

<style scoped lang="less">
    .btn {
        padding: 0;
    }

    .cooperation {
        min-height: 100vh;
        padding: .3rem .33rem 0 .26rem;


        label {
            display: block;
            font-size: .32rem;
            margin-bottom: .3rem;
        }

        input {
            padding-left: .3rem;
            height: .8rem;
            margin-bottom: .3rem;
        }

        input, textarea {
            width: 100%;
            background-color: #F3F3F3;
            border-radius: .1rem;
            font-size: .28rem;
        }

        textarea {
            padding: .26rem .3rem;
            height: 1.6rem;
            margin-bottom: 3.3rem;
        }

        p {
            span {
                color: #61C5FF;
            }
        }
    }
</style>