<template>
    <!--订单列表组件-->
    <div class="order-list">
        <div class="item mb30" v-for="(item,index) in order" :key="index">
            <!--编号-->
            <div class="number flex-between flexV-c" v-if="type==='order'">
                <span class="f28 f-bold">订单编号：{{item.order_number}}</span>
                <span class="f28" v-if="item.status==1">开门中</span>
                <span class="f28 wait-pay" v-else-if="item.status==2">待付款</span>
                <span class="f28 completed" v-else>已付款</span>
            </div>

            <div class="number flex-between flexV-c" v-else>
                <span class="f28 f-bold">设备编号：{{item.macno}}</span>
                <button @click="go('/afterSale',item.order_number)" v-if="item.f_status==2 && item.status==3">售后申诉
                </button>
                <button @click="go('/complaintResults',item.feedback_id)" v-if="item.f_status==0 && item.status==3">
                    申诉结果
                </button>
            </div>

            <!--商品-->
            <div class="goods flex-between flexV-c" v-for="(goods,goodsIndex) in item.goods" :key="goodsIndex"
                 @click="handleClick(item.order_id)">
                <div class="goods-detail">
                    <div class="pic mr20">
                        <img :src="goods.img" :alt="goods.title">
                    </div>
                    <div>
                        <p class="f30 f-bold pt25 ellipsis" style="max-width: 4rem">{{goods.title}}</p>
                        <p class="f26 color-red pt15">￥{{goods.price}}</p>
                    </div>
                </div>
                <span class="color-999 f28">x{{goods.num}}</span>
            </div>
            <slot></slot>
            <p class="f26 color-999 text-r pt35" v-if="item.discount_money">
                优惠金额：￥{{item.discount_money ||
                0.00}}</p>
            <div class="goods-num flex-between flexV-c">
                <span class="f28">共{{item.num}}件商品</span>
                <p class="f28">金额：<span class="color-red">¥{{item.total_price}}</span></p>
                <button v-if="item.status==2 && isShowPay==1" @click="go('/submitOrder',item.order_id)">付款</button>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "orderList",
        props: {
            order: Array,
            type: {
                type: String,
                default: 'order',
            },
            isShowPay: String   //是否显示付款按钮
        },
        methods: {
            /**
             * 路由跳转
             * @param path  //目标路由
             * @param id    //路由参数
             */
            go(path, id) {
                this.$router.push({
                    path: path,
                    query: {id: id}
                })
            },

            handleClick(evt) {
                this.$emit('click', evt);
            }
        }
    }
</script>

<style scoped lang="less">
    .color-red {
        color: #DD2A2A;
    }

    .order-list {
        padding: .15rem .24rem;

        .item {
            width: 100%;
            padding: 0 .26rem;
            background-color: #FFFFFF;
            box-shadow: 0 0 30px 0 rgba(28, 28, 28, 0.09);
            border-radius: .22rem;

            .number {
                height: .88rem;
                border-bottom: 1px solid rgba(0, 0, 0, .1);

                button {
                    width: 1.18rem;
                    height: .5rem;
                    background: #ffffff;
                    border: 1px solid #C30D23;
                    border-radius: .08rem;
                    font-size: .26rem;
                    color: #C30D23;
                }
            }

            .goods {
                padding: .3rem 0;

                .goods-detail {
                    display: flex;

                    .pic {
                        width: 1.2rem;
                        height: 1.2rem;
                        border: 1px solid rgba(221, 221, 221, 1);
                        border-radius: .1rem;

                        img {
                            width: 1.2rem;
                            height: 1.2rem;
                        }
                    }
                }

                border-bottom: 1px solid rgba(0, 0, 0, .1);
            }

            .goods-num {
                height: .9rem;

                button {
                    width: 1.18rem;
                    height: .5rem;
                    background: #E06D39;
                    color: #ffffff;
                    font-size: .28rem;
                    border-radius: .08rem;
                }
            }
        }

        .completed {
            color: #2C9225;
        }

        .wait-pay {
            color: #E06D39;
        }

    }
</style>