<template>
    <!--报故障-->
    <div class="failure-report pt20">
        <div class="number bg-fff pl30 f28 mb30">
            编号：{{macno}}
        </div>
        <div class="failure-problem bg-fff">
            <div class="uploader">
                <div class="photo mb30 pos-rel">
                    <i class="iconfont icon-camera_full color-999"></i>
                    <input type="file" @change="uploadPicture" accept="image/*"/>
                </div>
                <div v-for="(item,index) in imgArr.url" :key="index">
                    <img :src="item" alt="">
                </div>
            </div>

            <textarea maxlength="140" placeholder="请输入您的故障问题，并留下联系方式，我们会尽快回复您！" v-model="content"
                      @input="limitWord"></textarea>
            <span class="word-limit color-999 f24">{{wordNumber}}/140</span>
        </div>
        <input type="text" placeholder="请输入手机号" v-model="mobile">
        <dlc-btn msg="提交" @click="submit"></dlc-btn>
    </div>
</template>

<script>
    import Button from '@/components/button.vue'
    import axios from 'axios'

    export default {
        name: "failureReport",
        data() {
            return {
                macno: '99acda74080abbc5',   //设备号
                mobile: '',    //手机号
                content: '',   //故障内容,
                wordNumber: 0,    //textarea已输入字数
                imgArr: {
                    url: [], //完整图片链接
                    path: [] //图片路径
                },
                testImg: 'https://img.yzcdn.cn/upload_files/2017/03/16/Fs_OMbSFPa183sBwvG_94llUYiLa.jpeg?imageView2/2/w/100/h/100/q/75/format/jpg',
            }
        },
        components: {
            'dlc-btn': Button,
        },
        methods: {
            /**
             * 监听textarea字数变化
             */
            limitWord() {
                this.wordNumber = this.content.length;
            },
            /**
             * 图片上传
             */
            uploadPicture(e) {
                let that = this;
                let file = e.target.files[0];
                let param = new FormData(); // 创建form对象
                param.append("image[]", file, file.name); // 通过append向form对象添加数据
                let reader = new FileReader();
                reader.readAsDataURL(file);
                // reader.onloadend = function (evt) {
                //     console.log(evt.target)
                //     _this.imgs = evt.target.result;
                //     _this.show = false;
                // };
                let config = {
                    headers: {"Content-Type": "multipart/form-data"}
                };
                axios.post("http://sdxddshg.app.xiaozhuschool.com/wxsite/user/uploadImg", param, config
                ).then(res => {
                    console.log(res, '图片上传');
                    if (res.data.code == 1) {
                        that.imgArr.url.push(res.data.data.url);
                        that.imgArr.path.push(res.data.data.path);
                        that.$toast.success(res.data.msg);
                    } else {
                        that.$toast.fail(res.data.msg);
                    }
                });
            },

            /**
             * 提交设备故障
             */
            submit() {
                if (!this.content) {
                    this.$toast('请输入故障问题');
                    return false
                }
                if (!this.mobile) {
                    this.$toast('请输入联系电话');
                    return false
                }
                if (/^1[3|4|5|6|7|8][0-9]\d{8}$/.test(this.mobile)) {
                    this.reportFailure();
                } else {
                    this.$toast('请输入正确的手机号码');
                    return false
                }
            },
            /**
             * 设备故障申报AJAX
             */
            reportFailure() {
                let that = this;
                this.$ajax.post('/Device/api', {
                    api_name: 'addFault',
                    token: window.localStorage.token,
                    macno: that.macno,
                    mobile: that.mobile,
                    content: that.content,
                    img: this.imgArr.path.join()+','
                }).then((res) => {
                    console.log(res, '故障申报');
                    if (res.data.code == 1) {
                        that.mobile = '';
                        that.content = '';
                        that.imgArr = [];
                        that.$toast.success('提交成功');
                    } else {
                        that.$toast.fail(res.data.msg);
                    }
                })
            }
        },
        created() {
            // this.macno = this.$route.query.macno;
        }
    }
</script>

<style scoped lang="less">
    .failure-report {
        padding-bottom: 2rem;

        .number {
            height: .8rem;
            line-height: .8rem;
        }

        .failure-problem {
            padding: .3rem;
            margin-bottom: .2rem;
            position: relative;

            .uploader {
                display: flex;
                flex-wrap: wrap;

                > div {
                    width: 1.2rem;
                    height: 1.2rem;
                    margin-right: .2rem;
                    margin-bottom: .2rem;
                    border-radius: .1rem;

                    img {
                        width: 100%;
                        height: 1.2rem;
                        border-radius: .1rem;
                    }
                }

                > div:nth-of-type(5n) {
                    margin-right: 0;
                }
            }

            .photo {
                /*width: .74rem;*/
                /*height: .74rem;*/
                border: 1px solid #CFCFCF;
                /*border-radius: .1rem;*/
                position: relative;

                .iconfont {
                    position: absolute;
                    left: 50%;
                    top: .24rem;
                    transform: translateX(-50%);
                    font-size: .65rem;
                }

                input {
                    z-index: 999;
                    width: 1.2rem;
                    height: 1.2rem;
                    opacity: 0;
                }
            }

            textarea {
                width: 100%;
                height: 2.24rem;
                font-size: .24rem;
                line-height: 1.4;
                padding: .3rem .16rem;
                background: #ffffff;
                border: 1px solid #EEEEEE;
                border-radius: .1rem;
            }

            .word-limit {
                position: absolute;
                right: .64rem;
                bottom: .64rem;
            }
        }

        input {
            width: 100%;
            padding-left: .3rem;
            margin-bottom: 3.2rem;
            font-size: .28rem;
            height: .8rem;
            line-height: .8rem;
            background: #fff;
        }
    }
</style>