<template>
    <div>
        <div class="no-data text-c" v-if="noData">
            <img :src="bg" alt="">
            <p class="f32 text-c pt30">暂无优惠券~</p>
        </div>
        <div class="coupon" v-else>
            <div class="item flex-l" :class="item.status==1 || item.status_name==='已过期'?'used':'unused'"
                 v-for="(item,index) in coupon" :key="index">
                <div class="money">
                    <p class="mb20 text-c">
                        <span class="mr15">￥</span>
                        <span class="f56">{{item.coupon_money | formatNum}}</span>
                    </p>
                    <button>优惠券</button>
                </div>
                <div class="status">
                    <p class=" f30 f-bold">{{item.status_name}}</p>
                    <p class="f22 pl70">通用<span class="ml20">有效期至{{item.indate}}</span></p>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

    import {toFixed} from '@/libs/filter.js'
    import NoData from '@/assets/images/bg_no_data01.png'

    export default {
        name: "coupon",
        data() {
            return {
                //优惠券列表
                coupon: [],
                page: 1,
                bg: NoData,
                noData: false,   //数据为空缺省页
                dataFlag: true   //禁止请求
            }
        },
        methods: {
            /**
             * 获取优惠券
             * @param page  页数
             */
            getUserCoupon(page = 1) {
                let that = this;
                this.$toast({
                    type: 'loading',
                    message: '数据加载中...',
                    duration: 0,
                    loadingType: 'spinner'
                });
                this.$ajax.post('/User/api', {
                    api_name: 'coupon',
                    token: window.localStorage.token,
                    page: page,
                    pagesize: 10
                }).then(function (res) {
                    console.log(res, '优惠券');
                    that.$toast.clear();
                    that.dataFlag = true;
                    if (res.data.code == 1) {
                        let data = res.data.data;
                        console.log(data);

                        if (data.length == 0 && that.page == 1) {
                            that.noData = true;
                            that.dataFlag = false;
                            return
                        } else if (data.length == 0) {
                            that.dataFlag = false;
                            that.$toast('没有数据啦');
                            return
                        }
                        data.forEach((item) => {
                            that.coupon.push(item);
                        });
                        that.page++;
                    } else {
                        that.dataFlag = true;
                    }
                }).catch(() => {
                    that.$toast.clear();
                });
            },
            onScroll() {
                //可滚动容器的高度
                let innerHeight = document.getElementById('app').clientHeight;
                //屏幕尺寸高度
                let outerHeight = document.documentElement.clientHeight;
                //可滚动容器超出当前窗口显示范围的高度
                let scrollTop = document.documentElement.scrollTop;
                //scrollTop在页面为滚动时为0，开始滚动后，慢慢增加，滚动到页面底部时，出现innerHeight < (outerHeight + scrollTop)的情况，严格来讲，是接近底部。
                // console.log(innerHeight + " " + outerHeight + " " + scrollTop);
                if (innerHeight <= (outerHeight + scrollTop)) {
                    //加载更多操作
                    if (this.dataFlag) {
                        this.getUserCoupon(this.page);
                    }
                }
            },
        },
        created() {
            this.getUserCoupon();
            window.addEventListener('scroll', this.onScroll);
        },
        filters: {
            formatNum(number) {
                return toFixed(number, 0);
            }
        }
    }
</script>

<style scoped lang="less">
    .f56 {
        font-size: .56rem !important;
    }

    .no-data {
        height: 100vh;
        background: #fff;

        img {
            width: 4.2rem;
        }
    }

    .coupon {
        padding: .5rem .2rem;

        .item {
            width: 100%;
            height: 2rem;
            padding: .3rem;
            margin-bottom: .1rem;

            .money {
                flex-basis: 34.5%;
                padding-right: .32rem;
                border-right: 1px dashed #ffffff;

                button {
                    width: 1.3rem;
                    height: .4rem;
                    font-size: .28rem;
                    margin-left: .5rem;
                }
            }

            .status {
                padding-top: .2rem;

                p.f30 {
                    padding-left: 1.45rem;
                    margin-bottom: .38rem;
                }

            }

        }

        /*未使用优惠券背景图*/

        .unused {
            background: url("../../assets/images/bg_Coupon_01.png") no-repeat;
            background-size: 100% 100%;

            p {
                color: #ffffff;
            }

            p.f22 {
                color: #EDB59C;
            }

            button {
                color: #CF6637;
                background-color: #ffffff;
            }
        }

        /*已使用已过期优惠券背景图*/

        .used {
            background: url("../../assets/images/bg_Coupon_02.png") no-repeat;
            background-size: 100% 100%;

            .money {
                border-color: #E5E5E5;
            }

            p {
                color: #AAAAAA;
            }

            button {
                color: #ffffff;
                background-color: #727272;
            }
        }

    }
</style>