<template>
    <!--个人中心-->
    <div class="my-center pt20">
        <!--个人信息-->
        <div class="person-info flex-between  flexV-c mb20">
            <div class="left flexV-c">
                <div class="mr20" @click="clear">
                    <img :src="user.photo" alt="头像">
                </div>
                <span class="f30 f-bold">{{user.nickname}}</span>
            </div>
            <router-link to="phone">
                <span class="f28 f-bold color-999 mr25">修改手机</span>
                <i class="iconfont icon-right color-ccc"></i>
            </router-link>
        </div>
        <!--链接-->
        <router-link v-for="(item,index) in links" :key="index" :to="item.path" class="cell">
            <span class="f30 f-bold">{{item.name}}</span>
            <span class="f26 color-999 mr35">{{item.explain}}</span>
            <i class="iconfont icon-right color-ccc"></i>
        </router-link>
        <a :href="call" class="cell">
            <span class="f30 f-bold">客服电话</span>
            <span class="f26 color-999 mr35">{{tel}}</span>
            <i class="iconfont icon-right color-ccc"></i>
        </a>

    </div>
</template>

<script>

    export default {
        name: "myCenter",
        data() {
            return {
                //用户信息
                user: {
                    photo: '',  //头像
                    nickname: ''    //昵称
                },
                tel: '', //客服电话
                call: 'tel:',
                links: [
                    {
                        path: 'coupon',
                        name: '优惠券'
                    },
                    {
                        path: 'order',
                        name: '全部订单'
                    }
                ]
            }
        },
        methods: {
            /**
             * 获取用户信息
             */
            getUserInfo() {
                this.$ajax.post('/User/api', {
                    api_name: 'userInfo',
                    token: window.localStorage.token
                }).then((res) => {
                    this.user.photo = res.data.data.head_img;
                    this.user.nickname = res.data.data.nickname;
                })
            },
            /**
             * 获取客服电话
             */
            getPhone() {
                this.$ajax.post('/Public/get_phone').then((res) => {
                    this.tel = res.data.data.tel;
                    this.call = 'tel:' + res.data.data.tel;
                })
            },
            clear() {
                window.localStorage.removeItem('token');
                window.localStorage.removeItem('loginStatus')
            }
        },
        created() {
            this.getUserInfo();
            this.getPhone();
        }
    }
</script>

<style scoped lang="less">
    .color-ccc {
        color: #cccccc;
    }

    .my-center {
        .person-info {
            height: 1.3rem;
            background-color: #ffffff;
            padding: 0 .5rem;

            .left {
                img {
                    width: 1.06rem;
                    height: 1.06rem;
                    border-radius: 50%;
                }
            }
        }

        a.cell {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            height: 1rem;
            padding: 0 .5rem 0 .38rem;
            border-bottom: 1px solid #EEEEEE;
            background-color: #ffffff;
            color: #333333;
            position: relative;


            .iconfont {
                position: absolute;
                top: 29%;
                right: .5rem;
                font-size: .36rem;
            }
        }
    }
</style>