<template>
    <!--订单详情-->
    <div class="order-detail">
        <OrderList :order="order" type="device"></OrderList>
        <!--订单信息-->
        <div class="order-info" v-if="order[0]">
            <p class="mb30">订单编号：<span>{{order[0].order_number}}</span></p>
            <p class="mb30">订单状态：
                <span v-if="order[0].status==1">开门中</span>
                <span v-else-if="order[0].status==2">待付款</span>
                <span v-else>已完成</span>
            </p>
            <p>支付时间：<span>{{order[0].pay_time | formatDate}}</span></p>
        </div>
    </div>
</template>

<script>
    import OrderList from '@/components/orderList.vue'
    import {timeStr} from '@/libs/filter.js'

    export default {
        name: "orderDetail",
        data() {
            return {
                //订单ID
                orderId: '',
                // 订单
                order: [],
            }
        },
        methods: {
            /**
             * 获取订单详情
             * @param id  订单id
             */
            getOrderDetail(id) {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'orderDetail',
                    token: window.localStorage.token,
                    order_id: id
                }).then((res) => {
                    console.log(res, '订单详情');
                    if (res.data.code == 1) {
                        that.order.push(res.data.data);
                    }
                }).catch((err) => {

                })
            },
        },
        components: {
            OrderList
        },
        created() {
            //获取路由参数
            this.orderId = this.$route.query.id;
            this.getOrderDetail(this.orderId);
        },
        filters: {
            //时间戳转化
            formatDate(time) {
                // alert(timeStr(time, 'Y-m-d H:i'));
                return timeStr(time, 'Y-m-d H:i');
            }
        }
    }
</script>

<style scoped lang="less">
    .color-c30d23 {
        color: #C30D23;
    }

    .order-detail {
        padding: .14rem .24rem;

        .order-list {
            padding: 0;
        }

        /*订单信息*/

        .order-info {
            width: 100%;
            padding: .34rem .26rem;
            background-color: #ffffff;
            box-shadow: 0 0 30px 0 rgba(28, 28, 28, 0.09);
            border-radius: .22rem;

            p {
                font-size: .26rem;

                span {
                    color: #999999;
                }
            }
        }
    }
</style>