<template>
    <!--附近网点-->
    <div class="near-site">
        <div class="loading" v-show="loading">
            <div class="cssload-container">
                <div class="cssload-dot"></div>
                <div class="step" id="cssload-s1"></div>
                <div class="step" id="cssload-s2"></div>
                <div class="step" id="cssload-s3"></div>
            </div>
            <p class="f30 text-c" style="padding-top:8rem">地图加载中...</p>
        </div>

        <div id="map" ref="map">
            <div class="currentAddr flex-between flexV-c" v-if="showAddress">
                <div class="left">
                    <i class="iconfont icon-location"></i>
                    <p class="f28">{{siteInfo.address}}</p>
                </div>
                <div class="right" @click="go">
                    <i class="iconfont icon-daohang"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {Map} from '@/libs/map.js'
    import {getLocation, openLocation} from '@/libs/wechat/wxJsSdk.js'
    import MarkerIcon from '@/assets/images/marker.png'
    import CurrentIcon from '@/assets/images/current.png'


    export default {
        name: "nearSite",
        data() {
            return {
                makerIcon: MarkerIcon,
                currentIcon: CurrentIcon,
                loading: true,
                nearSite: [],    //附近网点
                // latitude: '',    //纬度
                // longitude: '',   //经度
                // currentAddress: '',  //当前地址
                siteInfo: {
                    lat: '',
                    lng: '',
                    title: '',
                    address: ''
                },
                showAddress: false, //显示当前网点地址
                location: [
                    {
                        lat: 23.04182522949167,
                        lng: 113.76823425292969
                    },
                    {
                        lat: 23.02713357885405,
                        lng: 113.73064041137695
                    },
                    {
                        lat: 23.026975595412548,
                        lng: 113.7495231628418
                    }
                ]
            }
        },
        methods: {
            /**
             * 获取附近网点
             */
            getNearSite(lat, lng, qq, map) {
                let that = this;
                this.$ajax.post('/Order/api', {
                    api_name: 'getNetwork',
                    token: window.localStorage.token,
                    lat: lat,
                    lng: lng,
                }).then(function (res) {
                    let data = res.data;
                    console.log(data, '附近网点');
                    if (data.code == 1 && data.msg == '获取成功') {
                        that.nearSite = data.data;
                        let siteSize = new qq.maps.Size(42, 45),
                            siteIcon = new qq.maps.MarkerImage(that.makerIcon, siteSize);
                        that.nearSite.forEach(function (item) {
                            let center = new qq.maps.LatLng(item.lat, item.lng);
                            let marker = new qq.maps.Marker({
                                icon: siteIcon,
                                position: center,
                                map: map
                            });
                            // // //获取标记的点击事件
                            qq.maps.event.addListener(marker, 'click', function (e) {
                                console.log(e, '标记');
                                that.latitude = e.latLng.lat;
                                that.longitude = e.latLng.lng;
                                that.currentAddress = item.address;
                                that.siteInfo = item;
                                that.showAddress = true;
                            });
                        });


                    } else {
                        // console.log(data);
                    }
                })
            },
            //初始化地图
            initMap(lat, lng) {
                let that = this;
                Map('Z6VBZ-5ADW6-C27SQ-MU2DG-H5XD6-2WFDS').then(qq => {
                    that.loading = false;
                    let map = new qq.maps.Map(document.getElementById("map"), {
                        // 地图的中心地理坐标。
                        center: new qq.maps.LatLng(lat, lng),
                        zoom: 15,
                        disableDefaultUI: true    //禁止所有控件
                    });
                    // that.$map = map;
                    let size = new qq.maps.Size(21, 34),
                        currentIcon = new qq.maps.MarkerImage(that.currentIcon, size);
                    // console.log(size);
                    let center = new qq.maps.LatLng(lat, lng);
                    let currentMarker = new qq.maps.Marker({
                        icon: currentIcon,
                        position: center,
                        map: map,
                    });
                    that.getNearSite(lat, lng, qq, map);
                });
            },


            go() {
                openLocation(this.siteInfo);
                // lat:22.989919662475586,
                // lng:113.72125244140625
                // 高德转化后
                // "locations": "113.726450737848,22.987173936632"
                // 火星坐标
                // lat:22.98719024658203,
                // lng:113.72645568847656
            }

        },

        created() {
            let that = this;
            if (process.env.NODE_ENV === 'development') {
                that.initMap(23.02713357885405, 113.73064041137695);
            } else {
                getLocation(function (res) {
                    that.initMap(res.latitude, res.longitude);
                });
            }
        },
        mounted() {
        },
    }
</script>

<style scoped lang="less">
    .near-site {
        background: #fff;
        height: 100vh;
    }

    #map {
        width: 100%;
        height: 100vh;
        position: relative;

        .currentAddr {
            position: absolute;
            left: .24rem;
            top: .2rem;
            width: 7.02rem;
            height: 1.26rem;
            padding: 0 .35rem;
            background-color: #ffffff;
            box-shadow: 0 0 21px 0 rgba(193, 193, 193, 0.32);
            border-radius: .1rem;
            z-index: 999999;

            .left {
                position: relative;
                padding-left: .35rem;

                .iconfont {
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    font-size: .32rem;
                    color: #999999;
                }
            }

            .right {
                width: .7rem;
                height: .7rem;
                position: relative;
                background-color: #3CAADC;
                border-radius: 50%;

                .icon-daohang {
                    position: absolute;
                    left: 12%;
                    top: .1rem;
                    color: #ffffff;
                    font-size: .5rem;
                }

            }
        }
    }

    .loading {
        padding-top: 3rem;
    }

    .cssload-container {

        width: 117px;
        height: 88px;
        position: absolute;
        left: 40%;
    }

    .cssload-dot {
        background: #CF6637;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        position: absolute;
        bottom: 29px;
        left: 0;
        transform-origin: center bottom;

        animation: cssload-dot 0.69s ease-in-out infinite;

    }


    .step {
        position: absolute;
        width: 60px;
        height: 60px;
        border-top: 2px solid #CF6637;
        top: 0;
        right: 0;
    }


    #cssload-s1 {
        animation: cssload-anim 2.07s linear infinite;
    }

    #cssload-s2 {
        animation: cssload-anim 2.07s linear infinite -0.69s;
    }

    #cssload-s3 {
        animation: cssload-anim 2.07s linear infinite -1.38s;
    }

    @keyframes cssload-dot {
        0% {
            transform: scale(1, .7);
        }
        20% {
            transform: scale(.7, 1.2);
        }
        40% {
            transform: scale(1, 1);
        }
        50% {
            bottom: 97px;
        }
        46% {
            transform: scale(1, 1);
        }
        80% {
            transform: scale(.7, 1.2);
        }
        90% {
            transform: scale(.7, 1.2);
        }
        100% {
            transform: scale(1, 0.7);
        }
    }


    @keyframes cssload-anim {
        0% {
            opacity: 0;
            top: 0;
            right: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            top: 88px;
            right: 88px;
            opacity: 0;
        }
    }

    @-o-keyframes cssload-anim {
        0% {
            opacity: 0;
            top: 0;
            right: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            top: 88px;
            right: 88px;
            opacity: 0;
        }
    }

    @-ms-keyframes cssload-anim {
        0% {
            opacity: 0;
            top: 0;
            right: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            top: 88px;
            right: 88px;
            opacity: 0;
        }
    }

    @-webkit-keyframes cssload-anim {
        0% {
            opacity: 0;
            top: 0;
            right: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            top: 88px;
            right: 88px;
            opacity: 0;
        }
    }

    @-moz-keyframes cssload-anim {
        0% {
            opacity: 0;
            top: 0;
            right: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            top: 88px;
            right: 88px;
            opacity: 0;
        }
    }

</style>