<template>
    <!--公用按钮组件-->
    <div class="btn">
        <button @click="handleClick"
                :class="[type ? 'btn-' + type : '']"
                :disabled="buttonDisabled"
        >{{ msg }}
        </button>
    </div>
</template>

<script>
    export default {
        name: "dlc-button",
        props: {
            //按钮背景颜色
            type: {
                type: String,
                default: 'default'
            },
            //按钮文字
            msg: String,
        },


        computed: {
            buttonDisabled() {
                return this.type==='default'?false:true
            }
        },


        methods: {
            handleClick(evt) {
                this.$emit('click', evt);
            }
        }
    }
</script>

<style scoped lang="less">
    .btn {
        width: 100%;
        padding: 0 .3rem;

        button {
            width: 6.9rem;
            height: .9rem;
            line-height: .9rem;
            font-size: .32rem;
            color: #fff;
            border-radius: .1rem;
            /*box-shadow: 0 5px 13px 0 rgba(207, 102, 55, 0.39);*/
        }


        .btn-default {
            background-color: #CF6637;
        }

        .btn-disable {
            background-color: #DBDBDB;
        }
    }
</style>