<template>
    <div class="check-list">
        <label class="checklist-title" v-text="title"></label>
        <div class="checklist-item">
            <label class="checklist-label" v-for="(option,index) in options" :key="index">
                <span class="checkbox-label f30" v-bind:class="{ 'color-999': option.noUse}"
                      v-text="option.label"></span>
                <span class="checkbox">
                <input type="radio" class="checkbox-input"
                       v-model="currentValue"
                       :disabled="option.disabled"
                       :value="option.id"
                       :id="option.value"
                       @change="sendValue(parseFloat(option.value))"
                >
                <span class="checkbox-core"></span>
            </span>
            </label>
        </div>
    </div>
</template>

<script>
    /**
     * checklist
     *
     * @desc 单选列表
     * @param {(string[]|object[])} options - 选项数组，可以传入 [{label: 'label', value: 'value', disabled: true}] 或者 ['ab', 'cd', 'ef']
     * @param {string[]} value - 选中值的数组
     * @param {string} title - 标题
     * @example
     * <checklist :v-model="value" :options="['a', 'b', 'c']"></checklist>
     */
    export default {
        name: 'checklist',
        props: {
            title: String,
            options: {
                type: Array,
                required: true
            },
            value: Number
        },
        data() {
            return {
                currentValue: this.value
            };
        },
        methods: {
            sendValue(value) {
                this.$emit('handleSelected', value);
            }
        },

        watch: {
            value(val) {
                this.currentValue = val;
            },
            currentValue(val) {
                this.$emit('input', val);

            }
        }
    }
</script>

<style scoped lang="less">
    .check-list {
        background: #fff;
        box-shadow: 0 0 .3rem 0 rgba(28, 28, 28, 0.09);
        border-radius: .22rem .22rem 0 0;

        .checklist-title {
            display: block;
            width: 100%;
            height: 1rem;
            line-height: 1rem;
            font-size: .36rem;
            font-weight: 500;
            text-align: center;
            background-color: #fff;
            color: #333333;
            border-bottom: 1px solid rgba(0, 0, 0, .1);
            border-radius: .22rem .22rem 0 0;
        }

        .checklist-item {
            height: 4rem;
            overflow-y: scroll;
        }

        .checklist-label {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            height: 1rem;
            padding: 0 .4rem 0 .44rem;
            background-color: #fff;
            border-bottom: 1px solid rgba(0, 0, 0, .1);

            .checkbox-input {
                display: none;
            }

            .checkbox-core {
                display: inline-block;
                background-color: #fff;
                border-radius: 100%;
                border: 1px solid #ccc;
                position: relative;
                width: 20px;
                height: 20px;
                vertical-align: middle;
            }

            .checkbox-core:after {
                border: 2px solid transparent;
                border-left: 0;
                border-top: 0;
                content: " ";
                top: 3px;
                left: 6px;
                position: absolute;
                width: 4px;
                height: 8px;
                transform: rotate(45deg) scale(0);
                transition: transform .2s;
            }

            .checkbox-input:checked + .checkbox-core:after {
                border-color: #ffffff;
                transform: rotate(45deg) scale(1);
            }

            .checkbox-input:checked + .checkbox-core {
                background-color: #CF6637;
                border-color: #CF6637;
            }

            .checkbox-input[disabled] + .checkbox-core {
                background-color: #d9d9d9;
                border-color: #ccc;
            }
        }
    }
</style>
<style>
    .van-popup {
        background-color: transparent !important;
    }

    .van-popup--bottom {
        width: 101%;
    }
</style>