<template>
    <!--扫码开门-->
    <div class="open-door">
        <div class="logo">
            <img :src="logo" alt="logo">
        </div>
        <p class="f28 f-bold">编号：{{macno}}</p>
        <dlc-btn type="default" msg="开门" @click="openDoor"></dlc-btn>
    </div>
</template>

<script>
    import Logo from '@/assets/images/logo-02.png'
    import Button from '@/components/button.vue'
    import {scanQRCode} from '@/libs/wechat/wxJsSdk.js'

    export default {
        name: "openDoor",
        data() {
            return {
                logo: Logo,
                macno: '',   //设备编号
                ws: null,
                userId: ''   //用户ID
            }
        },
        components: {
            'dlc-btn': Button,
        },
        methods: {
            /**
             *用户扫码开门
             * @param macno 设备号
             */
            openDoor() {
                let that = this;
                this.$toast({
                    type: 'loading',
                    message: '开门中...',
                    duration: 0,
                    loadingType: 'spinner'
                });
                this.$ajax.post('/Order/api', {
                    api_name: 'userOpenDoor',
                    macno: that.macno,   //设备号
                    token: window.localStorage.token,
                    type: 2
                }).then((res) => {
                    console.log(res, '扫码开门');
                    that.$toast.clear();
                    let code = res.data.code;
                    //判断用户是否绑定手机
                    switch (code) {
                        case 1:     //开门中
                            that.$router.push({
                                path: '/result',
                                query: {macno: that.macno, id: that.userId}
                            });
                            break;
                        case 4:     //未支付的订单
                            that.$router.push({
                                path: '/order',
                                query: {orderId: res.data.data.order_id, orderNo: res.data.data.order_number}
                            });
                            break;
                        case 88:    //未开通微信免密支付
                            that.$toast('请开通微信免密支付');
                            setTimeout(function () {
                                window.location.href = res.data.data.url;
                            }, 1000);
                            break;
                        case 99:    //未绑定手机
                            that.$toast('请先绑定手机');
                            setTimeout(function () {
                                that.$router.push({
                                    path: '/phone',
                                });
                            }, 1000);
                            break;
                        default:
                            that.$toast(res.data.msg);
                    }

                }).catch(() => {
                    that.$toast.clear();
                })
            },


            /**
             * 获取网点设备列表
             * @param id    //网点ID
             * @param page
             */
            getNetworkDevice(id, page = 1) {
                this.$ajax.post('/Order/api', {
                    api_name: 'getNetworkDevice',
                    token: window.localStorage.token,
                    network_id: id,
                    page: page
                }).then((res) => {
                    console.log(res, '网点设备列表');
                    if (res.data.code = 1) {
                        let data = res.data.data;
                        console.log(data);

                    }
                })
            },
            // connectinfo_xxxx
            // connectinfo_sdxddshg_close_用户ID
            socket(data, cb) {
                if ("WebSocket" in window) {
// var ws = new WebSocket("ws://120.77.72.190:9999");
                    var ws = new WebSocket("ws://120.77.248.106:9999");
                    ws.onopen = function () {
                        ws.send(data);
                    };
                    ws.onmessage = function (res) {
                        cb({code: 1, data: res.data});
                    };
                    ws.onclose = function (close) {
// alert(JSON.stringify(close));
                        console.log(JSON.stringify(close));
                        cb({code: 3, msg: "连接失败", data: close});
                    };
                    ws.error = function (err) {
// alert(JSON.stringify(err));
                        console.log(JSON.stringify(err));
                        cb({code: 4, msg: "连接失败", data: err});
                    };
                } else {
                    console.log(JSON.stringify(err));
                    cb({code: 2, msg: "当前浏览器不支持WebSocket!"});
                }
            }

        },
        created() {
            //获取路由参数
            this.macno = this.$route.query.macno;
            // this.getNetworkDevice(this.macno);
            // let userInfo = JSON.parse(window.localStorage.userInfo);
            // console.log(userInfo, '用户');
            // this.userId = userInfo.user_id;
        }
    }
</script>

<style scoped lang="less">
    .open-door {
        width: 100%;
        height: 100vh;
        background-color: #ffffff;
        padding: 1.55rem 0;

        .logo {
            width: 100%;
            text-align: center;
            margin-bottom: .42rem;

            img {
                width: 2.02rem;
            }
        }

        p {
            text-align: center;
            margin-bottom: 4.9rem;
        }
    }

</style>