<template>
    <!--申诉结果-->
    <div class="complaint-results">
        <div class="flex-between flexV-c f28 number">
            <span class="color-999">订单编号：{{result.order_no}}</span>
            <span class="handled" v-if="result.status==1">已处理</span>
            <span class="no-handle" v-else>未处理</span>
        </div>
        <div class="result pt30">
            <div class="mb40">
                <p class="f28 mb20">{{result.content}}</p>
                <ul>
                    <li v-for="(item,index) in result.img" :key="index"><img :src="item" alt="加载中"></li>
                </ul>
                <p class="color-999 f24">{{result.ctime}}</p>
            </div>
            <div v-if="result.status==1">
                <p class="content mb30 f22">{{result.dispose_content}}</p>
                <p class="color-999 f24">{{result.dispose_time}}</p>
            </div>
        </div>

    </div>
</template>

<script>


    export default {
        name: "complaintResults",
        data() {
            return {
                result: {}
            }
        },
        methods: {
            /**
             *获取申诉结果
             * @param id
             */
            feedbackResult(id) {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'feedbackDetail',
                    token: window.localStorage.token,
                    feedback_id: id
                }).then(function (res) {
                    console.log(res, '申诉结果');
                    if (res.data.code == 1) {
                        that.result = res.data.data;
                    }
                });
            }
        },
        created() {
            let id = this.$route.query.id;
            this.feedbackResult(id);
        }
    }
</script>

<style scoped lang="less">
    .complaint-results {
        padding: 0 .3rem;

        .number {
            height: .82rem;
            border-bottom: 1px solid #EEEEEE;
        }

        .result {
            ul {
                display: flex;
                flex-wrap: wrap;

                li {
                    width: 2.16rem;
                    height: 1.8rem;
                    margin-right: .16rem;
                    margin-bottom: .3rem;
                    background-color: #ffffff;

                    img {
                        width: 2.16rem;
                        height: 1.8rem;
                    }
                }

                li:nth-of-type(3n) {
                    margin-right: 0;
                }
            }

            .mb40 {
                padding-bottom: .44rem;
                border-bottom: 1px solid #EEEEEE;
            }

            p.content {
                line-height: 1.4;
            }
        }
    }

    .handled {
        color: #21A624;
    }

    .no-handle {
        color: #DD2A2A;
    }
</style>