<template>
    <!--开门结果-->
    <div class="door-result">
        <div class="logo">
            <img :src="logo" alt="logo">
        </div>
        <p class="f28 f-bold">编号：{{macno}}</p>

        <!--loading-->
        <div class="loading">
            <div class="sk-circle">
                <div class="sk-circle1 sk-child"></div>
                <div class="sk-circle2 sk-child"></div>
                <div class="sk-circle3 sk-child"></div>
                <div class="sk-circle4 sk-child"></div>
                <div class="sk-circle5 sk-child"></div>
                <div class="sk-circle6 sk-child"></div>
                <div class="sk-circle7 sk-child"></div>
                <div class="sk-circle8 sk-child"></div>
                <div class="sk-circle9 sk-child"></div>
                <div class="sk-circle10 sk-child"></div>
                <!--<div class="sk-circle11 sk-child"></div>-->
                <!--<div class="sk-circle12 sk-child"></div>-->
            </div>
            <p class="f28" style="margin-bottom: 2.95rem">请选择您的心仪商品并随手关门</p>
            <router-link to="fault" class="f28 color-999 text-c" style="display: block">我要报障</router-link>
        </div>
    </div>
</template>

<script>
    import Logo from '@/assets/images/logo-02.png'

    export default {
        name: "doorResult",
        data() {
            return {
                logo: Logo,
                macno: '',   //设备编号
                userId: ''   //用户ID
            }
        },
        methods: {

            /** 获取设备商品列表
             *
             * @param macno 设备号
             */
            getGoodsList(macno) {
                this.$ajax.post('/Device/api', {
                    api_name: 'deviceGoods',
                    token: window.localStorage.token,
                    macno: macno
                }).then((res) => {
                    console.log(res, '设备商品列表');
                })
            },


            /**
             * 获取用户信息
             */
            getUserInfo() {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'userInfo',
                    token: window.localStorage.token
                }).then((res) => {
                    console.log(res, '用户信息');
                    let data = res.data.data;
                    if (res.data.code == 1) {
                        console.log(data);
                        that.socket(`connectinfo_sdxddshg_close_${data.user_id}`, function (e) {
                            let data = JSON.parse(e);
                            console.log(data, 'ws-message');
                            if (data.code == 1) {
                                that.$toast(data.msg);
                                setTimeout(function () {
                                    that.$router.push({
                                        path: '/order'
                                    });
                                }, 800);
                            } else {
                                that.$toast(data.msg);
                            }

                        });
                    }
                })
            },

            // connectinfo_xxxx
            // connectinfo_sdxddshg_close_用户ID
            socket(data, cb) {
                if ("WebSocket" in window) {
                    // var ws = new WebSocket("ws://120.77.72.190:9999");
                    var ws = new WebSocket("ws://120.77.72.190:9999");
                    // ws://120.77.248.106:9999
                    ws.onopen = function () {
                        ws.send(data);
                    };
                    ws.onmessage = function (res) {
                        cb(res.data);
                    };
                    setInterval(() => {
                        ws.onmessage = function (res) {
                            cb(res.data);
                        };
                    }, 30000);
                    ws.onclose = function (close) {
                        // alert(JSON.stringify(close));
                        console.log(JSON.stringify(close));
                        cb({code: 3, msg: "连接失败", data: close});
                    };
                    ws.error = function (err) {
                        // alert(JSON.stringify(err));
                        console.log(JSON.stringify(err));
                        cb({code: 4, msg: "连接失败", data: err});
                    };
                } else {
                    console.log(JSON.stringify(err));
                    cb({code: 2, msg: "当前浏览器不支持WebSocket!"});
                }
            }


        },
        created() {
            this.getUserInfo();
            this.macno = this.$route.query.macno;
            // this.userId = this.$route.query.id;
            // this.socket(`connectinfo_sdxddshg_close_${this.userId}`);
            // this.socket(`connectinfo_sdxddshg_close_3`);
            // this.getGoodsList('99acda74080abbc5');
        }
    }
</script>

<style scoped lang="less">
    .door-result {
        width: 100%;
        height: 100vh;
        background-color: #ffffff;
        padding-top: 1.55rem;

        .logo {
            width: 100%;
            text-align: center;
            margin-bottom: .42rem;

            img {
                width: 2.02rem;
            }
        }

        p {
            text-align: center;
            margin-bottom: .9rem;
        }
    }

    .sk-circle {
        margin: 0 auto .76rem;
        width: 1.46rem;
        height: 1.46rem;
        position: relative;
    }

    .sk-circle .sk-child {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }

    .sk-circle .sk-child:before {
        content: '';
        display: block;
        margin: 0 auto;
        width: 15%;
        height: 15%;
        background-color: #CF6637;
        border-radius: 100%;
        animation: sk-circleBounceDelay 1.2s infinite ease-in-out both;
    }

    .sk-circle .sk-circle2 {
        transform: rotate(36deg);
    }

    .sk-circle .sk-circle3 {
        transform: rotate(72deg);
    }

    .sk-circle .sk-circle4 {
        transform: rotate(108deg);
    }

    .sk-circle .sk-circle5 {
        transform: rotate(144deg);
    }

    .sk-circle .sk-circle6 {
        transform: rotate(180deg);
    }

    .sk-circle .sk-circle7 {
        transform: rotate(216deg);
    }

    .sk-circle .sk-circle8 {
        transform: rotate(252deg);
    }

    .sk-circle .sk-circle9 {
        transform: rotate(288deg);
    }

    .sk-circle .sk-circle10 {
        transform: rotate(324deg);
    }

    /*.sk-circle .sk-circle11 {*/
    /*transform: rotate(300deg);*/
    /*}*/

    /*.sk-circle .sk-circle12 {*/
    /*transform: rotate(330deg);*/
    /*}*/

    .sk-circle .sk-circle2:before {
        animation-delay: -1.1s;
    }

    .sk-circle .sk-circle3:before {
        animation-delay: -1s;
    }

    .sk-circle .sk-circle4:before {
        animation-delay: -0.9s;
    }

    .sk-circle .sk-circle5:before {
        animation-delay: -0.8s;
    }

    .sk-circle .sk-circle6:before {
        animation-delay: -0.7s;
    }

    .sk-circle .sk-circle7:before {
        animation-delay: -0.6s;
    }

    .sk-circle .sk-circle8:before {
        animation-delay: -0.5s;
    }

    .sk-circle .sk-circle9:before {
        animation-delay: -0.4s;
    }

    .sk-circle .sk-circle10:before {
        animation-delay: -0.3s;
    }

    .sk-circle .sk-circle11:before {
        animation-delay: -0.2s;
    }

    .sk-circle .sk-circle12:before {
        animation-delay: -0.1s;
    }

    @keyframes sk-circleBounceDelay {
        0%, 80%, 100% {
            transform: scale(0);
        }
        40% {
            transform: scale(1);
        }
    }
</style>
