<template>
    <!--提交订单-->
    <div class="submit-order">
        <OrderList :order="order" type="device">
            <a class="cell flex-between flexV-c" @click="showCoupon">
                <span class="f28">优惠券</span>
                <div>
                    <span class="f28" v-if="selectedValue">¥{{selectedValue}}</span>
                    <i class="iconfont icon-right" v-else></i>
                </div>
            </a>
        </OrderList>

        <!--选择优惠券-->
        <van-popup v-model="showMask" position="bottom">
            <CheckedList title="优惠券" :options="coupon" @handleSelected="selected" v-model="checkedId"></CheckedList>
            <div class="bg-fff pt45 pb45">
                <dlc-button msg="确定" @click="confirmCoupon"></dlc-button>
            </div>
        </van-popup>

        <dlc-button msg="立即支付" @click="pay"></dlc-button>
    </div>
</template>

<script>

    import OrderList from '@/components/orderList.vue'
    import CheckedList from '@/components/checked.vue'
    import Button from '@/components/button.vue'

    import {chooseWXPay} from '@/libs/wechat/wxJsSdk.js'

    export default {
        name: "submitOrder",
        data() {
            return {
                //是否显示优惠券弹窗
                showMask: false,
                orderId: '', //订单ID
                // 订单
                order: [],
                //    优惠券列表
                coupon: [
                    {
                        label: '不使用优惠券',
                        value: 0,
                        noUse: true
                    },
                ],
                //选中的优惠券id
                checkedId: 0,
                //选中优惠券的金额
                selectedValue: 0,

            }
        },
        methods: {
            /**
             * 获取订单详情
             * @param id  订单id
             */
            getOrderDetail(id) {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'orderDetail',
                    token: window.localStorage.token,
                    order_id: id
                }).then((res) => {
                    console.log(res, '订单详情');
                    if (res.data.code == 1) {
                        let data = res.data.data;
                        console.log(data, '数据');
                        if (data.discount_money == "0.00") {
                            data.discount_money = 0;
                        }
                        that.order.push(data);
                    }
                }).catch((err) => {

                })
            },
            selected(val) {
                this.selectedValue = val;
            },

            /**
             *订单支付
             * @param orderId   //订单id
             * @param discountId    //优惠券id
             */
            pay() {
                let that = this;
                this.$toast({
                    type: 'loading',
                    message: '支付中...',
                    duration: 0,
                    loadingType: 'spinner'
                });
                // return
                this.$ajax.post('/Order/api', {
                    api_name: 'payOrder',
                    token: window.localStorage.token,
                    type: 1,
                    order_id: that.orderId,
                    discount_id: that.checkedId
                }).then((res) => {
                    console.log(res, '订单支付');
                    that.$toast.clear();
                    if (res.data.code == 1) {
                        chooseWXPay(res.data.data, function () {
                            that.$toast.success('支付成功');
                            setTimeout(function () {
                                that.$router.push({
                                    path: '/order',
                                })
                            }, 800);
                        });
                    } else {
                        that.$toast.fail('支付失败');
                    }
                }).catch(() => {
                    that.$toast.clear();
                })
            },

            /**
             * 获取优惠券
             * @param page  页数
             */
            getUserCoupon(page = 1) {
                let that = this;
                this.$ajax.post('/User/api', {
                    api_name: 'coupon',
                    token: window.localStorage.token,
                    page: page,
                    pagesize: 100
                }).then(function (res) {
                    console.log(res, '优惠券');
                    if (res.data.code == 1) {
                        let data = res.data.data;
                        data.forEach(function (item) {
                            if (item.status_name !== "已过期" || (item.status != 1 && item.status_name !== '已过期')) {
                                item.label = `¥${item.coupon_money}元`;
                                item.value = parseFloat(item.coupon_money);
                                that.coupon.push(item);
                            }
                            // item.label = `¥${item.coupon_money}元`;
                            // item.value = parseFloat(item.coupon_money);
                            // that.coupon.push(item);
                        });
                    }
                })
            },
            showCoupon() {
                //如果订单总金额小于优惠券金额，则不能使用优惠券
                this.coupon.forEach((item, index) => {
                    if (index > 0) {
                        if (item.value > this.totalMoney) {
                            item.disabled = true;
                        }
                    }
                });
                this.showMask = true;
            },
            //确认优惠券
            confirmCoupon() {
                //获取选择的优惠券和总金额
                let totalPrice = parseFloat(this.order[0].total_price);
                this.order[0].total_price = (totalPrice - this.selectedValue).toFixed(2);
                this.order[0].discount_money = this.selectedValue;
                this.showMask = false;
            }
        },
        components: {
            OrderList,  //订单组件
            CheckedList,    //优惠券组件
            'dlc-button': Button
        },
        computed: {
            totalMoney() {
                if (this.order.length > 0) {
                    return parseFloat(this.order[0].total_price);
                } else {
                    return 0
                }
            }
        },
        created() {
            //获取路由参数
            this.orderId = this.$route.query.id;
            this.getUserCoupon();
            this.getOrderDetail(this.orderId);
        },
    }
</script>

<style scoped lang="less">
    .order-list {
        margin-bottom: 2.32rem;
    }

    a.cell {
        width: 100%;
        height: .89rem;
        border-bottom: 1px solid rgba(0, 0, 0, .1);

        .iconfont {
            color: #BFBFBF;
        }
    }
</style>

